html, body {
    margin: 0;
    padding: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  
  .app-wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .main-content {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  
  footer {
    text-align: center;
    padding: 20px;
    background-color: #F5F5F5;
    border-top: 1px solid #E0E0E0;
    color: #333333;
    flex-shrink: 0;
  }
  