body {
  font-family: Arial, sans-serif;
  background-color: #f7f7f7;
  margin: 0;
  padding: 0;
}

.mock-interview-page {
  max-width: 1600px;
  margin: 20px auto;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.page-title {
  text-align: center;
  color: #333333;
  font-size: 1.8rem;
  margin-bottom: 20px;
}

.setup-section {
  padding: 10px;
}

.input-area {
  width: 100%;
  height: 100px;
  margin: 10px 0;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 1rem;
}

.dropdown {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 1rem;
}

.settings {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}

.label {
  font-weight: bold;
  margin-bottom: 5px;
}

.number-input {
  width: 50%;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

button {
  background-color: #007bff;
  color: #ffffff;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

button:hover {
  background-color: #0056b3;
}

textarea {
  resize: none;
}

.interview-section h3 {
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 1.2rem;
}

textarea {
  margin-top: 10px;
}

.evaluation-section {
  margin: 20px 0;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.section-title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
}

.evaluation-cards-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.evaluation-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border: 1px solid #eaeaea;
  transition: transform 0.2s ease-in-out;
}

.evaluation-card:hover {
  transform: scale(1.02);
}

.evaluation-question {
  font-size: 1.2rem;
  color: #007bff;
  margin-bottom: 10px;
}

.evaluation-combined-qa {
  background-color: #f4f4f4;
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 15px;
  font-family: monospace;
  white-space: pre-wrap; /* Preserve line breaks */
  font-size: 0.95rem;
  color: #444;
}

.evaluation-scores {
  margin-top: 15px;
}

.evaluation-score {
  background-color: #f3f3f3;
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 10px;
}

.score-title {
  font-size: 1rem;
  font-weight: bold;
  color: #444;
  margin-bottom: 5px;
}

.score-value,
.score-rationale {
  font-size: 0.9rem;
  color: #666;
}
.features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Responsive grid */
  gap: 20px;
}

@media (max-width: 768px) {
  .features {
    grid-template-columns: 1fr; /* Single column for smaller screens */
  }
}

