/* Resume Tools Page */
.resume-tools-page {
  font-family: Arial, sans-serif;
  max-width: 1800px; /* Increase max-width for the page */
  margin: 0 auto; /* Center the page */
  padding: 30px; /* Add more padding for better spacing */
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Headings */
h1, h2, h3 {
  color: #333;
}

/* Textarea */
textarea {
  width: 100%; /* Take the full width of the container */
  height: 100px;
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  resize: none;
}

/* Button */
button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

/* Section Analysis */
.section-analysis {
  margin-bottom: 20px;
}

/* Updated Resume Section Textarea */
.updated-resume-section textarea {
  background-color: #e9ecef;
  border: 1px solid #ccc;
}

/* Features Section */
.features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(600px, 1fr)); /* Wider grid items */
  gap: 20px;
}

/* Responsive Features Grid */
@media (max-width: 1600px) {
  .features {
    grid-template-columns: 1fr; /* Single column for smaller screens */
  }
}

/* Resume Tools Container */
.resume-tools-container {
  max-width: 1700px; /* Increase the maximum width */
  margin: 0 auto; /* Center the container */
  padding: 25px; /* Adjust padding for spacing */
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Title */
.resume-tools-container h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 28px; /* Increase font size for larger screens */
  font-weight: bold;
  color: #333;
}

/* Textarea for Resume Input */
.resume-tools-container textarea {
  width: 98%; /* Take slightly more space */
  height: 400px;
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px; /* Slightly larger font */
  resize: vertical; /* Allow resizing */
}

/* Analyze Button */
.resume-tools-container button {
  width: 100%;
  padding: 12px; /* Adjust padding for larger buttons */
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 18px; /* Slightly larger font */
  font-weight: bold;
  cursor: pointer;
}

.resume-tools-container button:hover {
  background-color: #0056b3;
}
