
.landing-page {
  font-family: 'Roboto', Arial, sans-serif;
  text-align: center;
  padding: 20px;
  color: #333;
}

.intro h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  color: #007bff;
}

.intro p {
  font-size: 1.2rem;
  margin-bottom: 40px;
}

.features {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.column {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  text-align: left;
}

.column h2 {
  color: #007bff;
  margin-bottom: 10px;
}

.column p {
  font-size: 1rem;
  margin-bottom: 20px;
}

.workflow ul {
  list-style: none;
  padding: 0;
  margin: 0;  
  line-height: 1.8;
}

.workflow ul li {
  padding: 10px;
  border-left: 4px solid #007bff;
  background-color: #f9f9f9;
  margin-bottom: 10px;
  border-radius: 4px;
}

.features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Responsive grid */
  gap: 20px;
}

@media (max-width: 768px) {
  .features {
    grid-template-columns: 1fr; /* Single column for smaller screens */
  }
}
