button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 8px 16px; /* Ensures the button size remains consistent */
  min-width: 120px;  /* Prevents button shrinking when text is replaced */
}

.spinner {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid #f3f3f3;
  border-top: 2px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
